import { Navigation } from "@/components/navigation"
import { AnalyticsCard } from "@/components/analytics-card"
import { ExpenseChart } from "@/components/expense-chart"
import { TrendingUpIcon, TrendingDownIcon, DollarSignIcon } from 'lucide-react'
import { createClient } from '@/lib/supabase/server'
import { redirect } from 'next/navigation'
import { dailyAnalytics } from '@/lib/analytics'

// Helper function to format dates for daily chart
const formatDailyDate = (dateString: string) => {
  const date = new Date(dateString)
  const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
  return days[date.getDay()]
}

export default async function DailyAnalyticsPage() {
  const supabase = await createClient()
  
  // Get user data
  const { data: { user } } = await supabase.auth.getUser()
  
  if (!user) {
    redirect('/login')
  }
  
  // Get analytics data
  const analyticsData = await dailyAnalytics(user.id)
  
  const { totalIncome, totalExpense, net, previousPeriodComparison, graphData } = analyticsData
  
  return (
    <div className="flex min-h-screen flex-col md:flex-row">
      <Navigation />
      <main className="flex-1 p-4 pb-20 md:p-8 md:pb-8">
        <div className="mx-auto max-w-6xl space-y-6">
          <h1 className="text-3xl font-bold">Daily Analytics</h1>

          <div className="grid gap-4 md:grid-cols-3">
            <AnalyticsCard
              title="Total Income"
              value={`$${totalIncome.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`}
              change={`${previousPeriodComparison >= 0 ? '+' : ''}${previousPeriodComparison.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })} from yesterday`}
              changeType={previousPeriodComparison >= 0 ? "positive" : "negative"}
              icon={TrendingUpIcon}
            />
            <AnalyticsCard
              title="Total Expense"
              value={`$${totalExpense.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`}
              change={`${previousPeriodComparison >= 0 ? '+' : ''}${Math.abs(previousPeriodComparison).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })} from yesterday`}
              changeType={previousPeriodComparison >= 0 ? "positive" : "negative"}
              icon={TrendingDownIcon}
            />
            <AnalyticsCard
              title="Net Balance"
              value={`$${net.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`}
              change={`${previousPeriodComparison >= 0 ? '+' : ''}${previousPeriodComparison.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })} from yesterday`}
              changeType={previousPeriodComparison >= 0 ? "positive" : "negative"}
              icon={DollarSignIcon}
            />
          </div>

          <ExpenseChart
            data={graphData.map(item => ({
              day: formatDailyDate(item.date),
              income: item.income,
              expense: item.expense
            }))}
            title="Daily Trend (Last 7 Days)"
            type="line"
            dataKeys={[
              { key: "income", color: "#10b981", label: "Income" },
              { key: "expense", color: "#ef4444", label: "Expense" },
            ]}
            xAxisKey="day"
          />

          <div className="rounded-lg border bg-card p-6">
            <h3 className="text-lg font-semibold mb-2">Insights</h3>
            <p className="text-muted-foreground">
              {net >= 0 
                ? "Great job! You're spending less than you're earning today." 
                : "Be careful! You're spending more than you're earning today. Consider reviewing your expenses."}
            </p>
          </div>
        </div>
      </main>
    </div>
  )
}